
.mdl-layout{
  min-width: 320px;
}
.header-color {
  background: #009fff;
  background: -webkit-gradient(linear, right top, left top, from(#ec2f4b), to(#009fff));
  background: linear-gradient(to left, #ec2f4b, #009fff);
}

a {
  text-decoration: none;
}

.landing-grid {
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 1024px) {
  .landing-grid {
    background: #009fff;
    background: -webkit-gradient(linear, right top, left top, from(#ec2f4b), to(#009fff));
    background: linear-gradient(to left, #ec2f4b, #009fff);
  }
  .videoTag {
    display: none;
  }
}

.avatar-image {
  margin-top: 5em;
  height: 300px;
  /* border-bottom: 1px solid white; */
  border-radius: 45%;
}

.banner-text {
  background-color: black;
  opacity: 0.8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.sacial-links {
  display: flex;
  justify-content: space-between;
  width: 30%;
  margin: auto;
}

@media only screen and (max-width: 550px) {
  .landing-grid {
    height: auto;
  }
  .banner-text h1 {
    font-size: 35px;
  }
  .sacial-links {
    width: 50%;
  }
  .avatar-image {
    height: 150px;
    margin-top: 2em;
  }
  .banner-text {
    width: 90%;
  }
}

.sacial-links .fa-linkedin-square {
  color: #0066ff;
  font-size: 5em;
}
.sacial-links i {
  color: white;
  font-size: 5em;
}

