/* ////////////////////////////////////////////////////////// */
/* Contact                                                   */

.contact-body {
  text-align: center;
  margin: auto;
  /* position: fixed; */
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: auto;
  background: white;
  color: black;
  border-top: 6px solid lightsalmon;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: "Suez One", serif;
}
.contact-grid p {
  font-family: "Arvo", serif;
  font-weight: bold;
}
.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}
.contact-list {
  text-align: center;
}

.contact-list i {
  font-size: 45px;
  padding-right: 5rem;
}

@media only screen and (max-width: 1039px) {
  .contact-list i {
    font-size: 35px;
    padding-right: 2rem;
  }
  .listItemContent span {
    font-size: 1.9rem;
  }
}

@media only screen and (max-width: 839px) {
  .contact-list i {
    font-size: 30px;
    padding-right: 2rem;
  }
  .listItemContent span {
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 720px) {
  .contact-list i {
    font-size: 30px;
    padding-right: 1.5rem;
  }
  .listItemContent span {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 590px) {
  .contact-list i {
    font-size: 25px;
    padding-right: 1rem;
  }
  .listItemContent span {
    font-size: 1rem;
  }
}
@media only screen and (max-width: 400px) {
  .contact-list i {
    font-size: 20px;
    padding-right: 0.7rem;
  }
  .listItemContent span {
    font-size: 0.7rem;
  }
}

/* //////////////////////////////////////////////////////// */
/*     Resume page Css                                     */

/* Left part */

.resume-left-div {
  text-align: center;
}
.resume-avatar {
  height: 200px;
}
.resume-fullname {
  padding-top: 2em;
}
.resume-hr {
  border-top: 3px solid blue;
  width: 50%;
}

/* Right part */

.resume-right-col {
  background: #232526; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    right top, left top,
    from(#414345),
    to(#232526)
  );
  background: linear-gradient(
    to left,
    #414345,
    #232526
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  padding: 2em;
  border-radius: 40px;
}

.resume-grid {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.schoolname {
  margin: 0;
}

.skills-grid {
  display: grid;
  grid-template-columns: 1fr;
}

.skill-container {
  width: 100%;
}

#btn{
  position: relative;
  top: 80px;
  left: 2px;
  -webkit-transform: translate(-50% -50%);
          transform: translate(-50% -50%);
  color: #1670f0;
  padding: 8px 20px;
  font-size: 15px;
  letter-spacing: 2px;
  text-transform: uppercase;
  box-shadow: 0px 20px 50px rgba(0,0,0,.5);
  overflow: hidden;
}
#btn:before{
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  bottom: 2px;
  width: 50%;
  background: rgba(255, 255, 255, 0.05)
}
#btn span:nth-child(1){
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: -webkit-gradient(linear, left top, right top, from(#0c002b) , to(#1779ff));
  background: linear-gradient(to right, #0c002b , #1779ff);
  -webkit-animation: animate1 2s linear  infinite;
          animation: animate1 2s linear  infinite;
}
@-webkit-keyframes animate1
{
 0%{
   -webkit-transform: translateX(-100%);
           transform: translateX(-100%)
 }
 100%{
  -webkit-transform: translateX(100%);
          transform: translateX(100%)
 }
}
@keyframes animate1
{
 0%{
   -webkit-transform: translateX(-100%);
           transform: translateX(-100%)
 }
 100%{
  -webkit-transform: translateX(100%);
          transform: translateX(100%)
 }
}
